* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
}

div {
    position: absolute;
    // display: block;
    width: 80px;
    height: 80px;
    background-color:  #DE1D51;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
    animation: rotateAnim 1s ease-in-out 0s infinite normal;
}


// p {
//     position: relative;
//     z-index: 2;
// }

@keyframes rotateAnim {
    0% {
        transform: translate(-50%, -50%) rotate(0deg);
    }

   50% {
        width: 200px;
        height: 200px;
    }

    100% {
        transform:translate(-50%, -50%) rotate(360deg);
    }
}
